import platform from "../img/platform.png";
import platformSmall from "../img/platformSmallTall.png";
import background from "../img/background.png";
import hills from "../img/hills.png";
import runLeft from "../img/spriteRunLeft.png";
import runRight from "../img/spriteRunRight.png";
import standLeft from "../img/spriteStandLeft.png";
import standRight from "../img/spriteStandRight.png";
import frt from "../img/frt.png";

const canvas = document.querySelector("canvas");
const c = canvas.getContext("2d");
canvas.width = 1024;
canvas.height = 576;
let frameCount = 0;

let scrollOffset = 0;
let gravity = 1;
const keys = {
  left: {
    pressed: false,
  },
  right: {
    pressed: false,
  },
};

class Player {
  constructor() {
    this.speed = 5;
    this.jump = 15;
    this.position = {
      x: 100,
      y: 100,
    };

    this.velocity = {
      x: 0,
      y: 0,
    };

    this.width = 66;
    this.height = 150;
    this.frames = 0;
    this.sprites = {
      stand: {
        right: {
          image: createImage(standRight),
          cropWidth: 177,
          width: 66,
        },
        left: {
          image: createImage(standLeft),
          cropWidth: 177,
          width: 66,
        },
      },
      run: {
        right: {
          image: createImage(runRight),
          cropWidth: 341,
          width: 127.875,
        },
        left: {
          image: createImage(runLeft),
          cropWidth: 341,
          width: 127.875,
        },
      },
    };

    this.currentSprite = this.sprites.stand.right;
    this.frt = createImage(frt);
  }

  draw() {
    c.drawImage(
      this.currentSprite.image,
      this.currentSprite.cropWidth * this.frames,
      0,
      this.currentSprite.cropWidth,
      400,
      this.position.x,
      this.position.y,
      this.width,
      this.height
    );

    //c.drawImage(this.frt, this.position.x + 5, this.position.y, 55, 55);
  }

  update() {
    if (
      this.currentSprite === this.sprites.run.right ||
      this.currentSprite === this.sprites.run.left
    )
      this.width = this.currentSprite.width;

    if (
      this.currentSprite === this.sprites.stand.right ||
      this.currentSprite === this.sprites.stand.left
    )
      this.width = this.currentSprite.width;
    this.frames++;

    if (
      (this.frames > 59 && this.currentSprite === this.sprites.stand.right) ||
      (this.frames > 59 && this.currentSprite === this.sprites.stand.left)
    )
      this.frames = 0;
    else if (
      (this.frames > 29 && this.currentSprite === this.sprites.run.right) ||
      (this.frames > 29 && this.currentSprite === this.sprites.run.left)
    )
      this.frames = 0;

    this.draw();
    this.position.y += this.velocity.y;
    this.position.x += this.velocity.x;

    if (this.position.y + this.height + this.velocity.y <= canvas.height) {
      if (this.position.y > 0 && this.position.y < 50) gravity = 0.1;
      else if (this.position.y >= 50 && this.position.y < 200) gravity = 0.3;
      else gravity = 1;

      this.velocity.y += gravity;
    }
  }
}

class Platform {
  constructor({ x, y, image }) {
    this.position = {
      x,
      y,
    };
    this.image = image;
    (this.width = this.image.width), (this.height = this.image.height);
  }

  draw() {
    c.drawImage(this.image, this.position.x, this.position.y);
  }
}

class GenericObject {
  constructor({ x, y, image }) {
    this.position = {
      x,
      y,
    };
    this.image = image;
    (this.width = this.image.width), (this.height = this.image.height);
  }

  draw() {
    c.drawImage(this.image, this.position.x, this.position.y);
  }
}

let player = new Player();
player.draw();

let platformImage = createImage(platform);
let platformSmallImage = createImage(platformSmall);

let platforms = [];

let genericObjects = [];

let winPosition;

function init() {
  player = new Player();
  player.draw();

  platformImage = createImage(platform);

  platforms = designLevels();

  genericObjects = [
    new GenericObject({ x: -2, y: -2, image: createImage(background) }),
    new GenericObject({ x: -2, y: -2, image: createImage(hills) }),
  ];

  scrollOffset = 0;
}

init();
animate();

function designLevels() {
  let levels = [new Platform({ x: 0, y: 455, image: platformImage })];

  for (let i = 1; i < 100; i++) {
    let x =
      levels[levels.length - 1].position.x +
      levels[levels.length - 1].width +
      generateRandom(80, 250);

    if (i % 3 == 0 || i % 5 == 0) {
      levels.push(new Platform({ x: x, y: 300, image: platformSmallImage }));
    } else {
      levels.push(new Platform({ x: x, y: 455, image: platformImage }));
    }

    winPosition = x;
  }
  return levels;
}

let then = Date.now();
function createImage(imageSource) {
  const image = new Image();
  image.src = imageSource;
  return image;
}

function animate() {
  requestAnimationFrame(animate);

  let now = Date.now();
  let elapsed = now - then;

  if (elapsed > 7) {
    then = Date.now();
    c.fillStyle = "white";
    c.fillRect(0, 0, canvas.width, canvas.height);

    genericObjects.forEach((object) => {
      object.draw();
    });

    platforms.forEach((platform) => {
      platform.draw();
      detectCollisions(platform);
    });

    player.update();

    checkWhenKeysReleased();
    checkIfPlayerWins();
    checkIfPlayerLoses();

    c.font = "48px Helvetica";
    c.fillText("Score: " + scrollOffset, 10, 50);
  }
}

function checkIfPlayerWins() {
  if (scrollOffset >= winPosition) console.log("You Win");
}

function checkIfPlayerLoses() {
  if (player.position.y > canvas.height) {
    init();
  }

  if (player.position.y + player.height < 0) {
    init();
  }
}

function detectCollisions(platform) {
  if (
    player.position.y + player.height <= platform.position.y &&
    player.position.y + player.height + player.velocity.y >=
      platform.position.y &&
    player.position.x + player.width >= platform.position.x &&
    player.position.x <= platform.position.x + platform.width
  )
    player.velocity.y = 0;
}

function checkWhenKeysReleased() {
  if (keys.left.pressed && player.position.x > 100) {
    player.velocity.x = -player.speed;
  } else if (keys.right.pressed && player.position.x < 400) {
    player.velocity.x = player.speed;
  } else {
    player.velocity.x *= 0.9;

    movePlatform();
  }
}

function movePlatform() {
  if (keys.right.pressed) {
    platforms.forEach((platform) => {
      platform.position.x -= player.speed;
    });
    genericObjects.forEach((object) => {
      object.position.x -= player.speed * 0.66;
    });

    scrollOffset += player.speed;
  } else if (keys.left.pressed && scrollOffset > 0) {
    platforms.forEach((platform) => {
      platform.position.x += player.speed;
    });
    genericObjects.forEach((object) => {
      object.position.x += player.speed * 0.66;
    });

    scrollOffset -= player.speed;
  }
}

addEventListener("keydown", (event) => {
  if (event.key === "ArrowLeft" || event.key == "a") {
    keys.left.pressed = true;
    player.currentSprite = player.sprites.run.left;
  } else if (event.key === "ArrowRight" || event.key == "d") {
    keys.right.pressed = true;
    player.currentSprite = player.sprites.run.right;
  } else if (event.key === " ") {
    player.velocity.y -= player.jump;
    player.currentSprite = player.sprites.run.right;
  }
});

addEventListener("keyup", (event) => {
  if (event.key === "ArrowLeft" || event.key == "a") {
    keys.left.pressed = false;
    player.currentSprite = player.sprites.stand.left;
  } else if (event.key === "ArrowRight" || event.key == "d") {
    keys.right.pressed = false;
    player.currentSprite = player.sprites.stand.right;
  }
});

function generateRandom(min, max) {
  return Math.floor(Math.random() * (max - min)) + min;
}
